

.sidebar{
    /* height:100vh; */
    display: inline-flex;
    flex-direction: column;
    justify-content: space-between;
    /* background-color: #dfe4ea; */
    background-color: #e4eced;
    padding:25px 15px ;
}
.sidebar img{
    width: 20px;
}
.sidebar .menu{
    display: block;
    margin-left: 15px;
    cursor: pointer;
}
.sidebar .new-chat{
    margin-top: 30px;
    display: inline-flex;
    align-items: center;
    gap: 10px;
    padding: 5px 20px;
    background-color: aliceblue;
    border-radius: 50px;
    font-size: 14px;
    color: grey;
    cursor: pointer;



}

.sidebar .recent{
    display: flex;
    flex-direction:  column;
    animation: fadein 1.5s;
}

.sidebar .recent-title{
    margin-top: 30px;
    margin-bottom: 20px;
}
.sidebar .recent-entry{
    display: flex;
    align-items:center;
    gap: 10px;
    padding: 5px 10px;
    padding-right: 40px;
    border-radius: 50px;
    color: black;
    cursor: pointer;

}

.sidebar .recent-entry:hover{
    background-color: white;

}

.sidebar .bottom{
    display: flex;
    flex-direction: column;
}

.sidebar .bottom{
    padding-right: 10px;
    cursor: pointer;
}

@media(max-width:600px){
    .sidebar{
        display: none;
    }
}